<script>
import store from '@/store'

export default {
    name: 'menu-lateral',
    mounted: function() {
        if( window.innerWidth - 700 ){
            store.commit('abrirMenu')
        }
    },
    methods: {
        cerrarMenu() {
            if( window.innerWidth < 700 ) {
                store.commit('cerrarMenu')
            }
        },
        eventosAbrir(e) {
            const ul = document.getElementById( e.target.dataset.id )
            const altura = ul.offsetHeight

            if( ul.parentNode.offsetHeight !== altura ) {
                ul.parentNode.style.height = altura + 'px'
            }else{
                ul.parentNode.style.height = 0
            }
        }
    }
}
</script>


